import React, { useState } from "react";
import { getFolders } from "./api";
import "./sidebar.scss";

const File = ({ file, setContent }) => {
  return (
    <div className="file" onClick={() => setContent(file.title)}>
      <i className="fas fa-file"></i>
      <span className="title">{file.title}</span>
    </div>
  );
};

const Folder = ({ folder = {}, depth = 0, setContent }) => {
  const subFolders = folder.folders;
  const files = folder.files;
  const [expand, setExpand] = useState(false);
  return (
    <div style={{ padding: `4px 0px 4px ${depth * 12}px` }}>
      <div className="folder" onClick={() => setExpand(!expand)}>
        {!expand ? (
          <i className="fas fa-folder" />
        ) : (
          <i className="fas fa-folder-open" />
        )}
        <span className="title">{folder.title}</span>
      </div>
      {expand && (
        <React.Fragment>
          {!files && !subFolders && <span className="empty-folder">Empty</span>}
          {files &&
            files.map(file => (
              <File key={file.id} file={file} setContent={setContent} />
            ))}
          {subFolders &&
            subFolders.map(folder => (
              <Folder
                key={folder.id}
                folder={folder}
                depth={depth + 1}
                setContent={setContent}
              />
            ))}
        </React.Fragment>
      )}
    </div>
  );
};

export default () => {
  const [content, setContent] = useState("");
  const folders = getFolders();
  return (
    <div className="folder-structure">
      <div className="sidebar">
        {folders.map(folder => (
          <Folder
            key={folder.id}
            folder={folder}
            depth={0}
            setContent={setContent}
          />
        ))}
      </div>
      <div className="content">{content}</div>
    </div>
  );
};
