import React, { useContext, useEffect, useState } from 'react';

import Store from '../../store';
import API from '../../utils/api';

import ChatHeader from './ChatHeader';
import MsgForm from './MsgForm';

const Sent = ({ msg }) => {
  return (
    <React.Fragment>
      <li>
        <p className="sent">{msg.content}</p>
      </li>
    </React.Fragment>
  )
};

const Replies = ({ msg }) => {
  return (
    <React.Fragment>
      <li>
        <p className="reply">{msg.content}</p>
      </li>
    </React.Fragment>
  )
};


export default () => {
  const { store } = useContext(Store);
  const { user, activeChannel } = store;
  const [messages, setMessages] = useState([]);

  const friend = activeChannel.participants.find(p => p._id !== user._id);
  const IO = store.Socket;

  useEffect(async () => {
    // active channel changed, subscribe to channel and receive all messages
    const messages = await API.get(`channels/${activeChannel._id}/messages`);
    IO.emit('join_private_channel', { channel: activeChannel._id });
    setMessages(messages);

    IO.on('receive_message', (data) => {
      // only update state if message received is from active channel user
      if (data.from === friend._id) {
        messages.push({ content: data.content });
        setMessages(messages);
      }
    });
  }, [activeChannel._id]);

  const sendMessage = msg => {
    messages.push({
      content: msg,
      from: user._id,
      channel: activeChannel._id
    });
    setMessages(messages);
    IO.emit('send_message', { content: msg, channel: activeChannel._id });
  };
  return (
    <React.Fragment>
      <ChatHeader friend={friend} />
      <div className="conversation">
        <ul>
          {messages.map((msg, id) => <React.Fragment key={id}>
            {
              msg.from === user._id ? <Sent msg={msg} /> : <Replies msg={msg} />
            }
          </React.Fragment>)}
        </ul>
      </div>
      <MsgForm send={sendMessage} />
    </React.Fragment>
  )
}
